<script>
import swiper from "../assets/js/swiper.min.js";
export default {
  data() {
    return {
      mainDropdownBtnStatus: false,
      mainDropdownBtnStatus2: false,
      myswiper: "",
      contents: [
        [
          {
            name1: "一文看透Q2股汇金油",
            href1: "javascript::;",
            id: "1"
          },
          {
            name2: "开户有礼 入金即送$300",
            href2: "javascript::;",
            id: "2"
          }
        ],
        [
          {
            name1: "FXTM富拓APP 非凡体验",
            href1: "javascript::;",
            id: "3"
          },
          {
            name2: "五月纳指点差减半",
            href2: "javascript::;",
            id: "4"
          }
        ]
      ],
      // 是否固定
      isFixed: false,
      isHeaderFixed: false,
      navbarMobileFixed: true,
      isClose: false,
      i: 0,

      DropdownBtnStatus: false,

      DropdownItemBtnStatus: false,
      DropdownItemBtnStatus2: false,
      DropdownItemBtnStatus4: false,
      DropdownItemBtnStatus5: false,
      DropdownItemBtnStatus3: false,
      DropdownItemBtnStatus6: false,
      showcdrpstatus1: false
    };
  },

  created() {},
  mounted() {
    let _this = this;

    this.$nextTick(function () {
      _this.initData();
    });
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    initData() {
      let _this = this;
      _this.myswiper = new swiper("#swiper1", {
        slidesPerView: 1,

        loop: true,
        autoplay: true,
        loopFillGroupWithBlank: true,
        observer: true, //修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, //修改swiper的父元素时，自动初始化swiper
        onSlideChangeEnd: function (swiper) {
          swiper.update();
          _this.myswiper.startAutoplay();
          _this.myswiper.reLoop();
        }
      });
    },

    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;

      var offsetTop = document.querySelector(".header_navbar").offsetHeight;
      var scroll = scrollTop - this.i;
      this.i = scrollTop;
      if (scroll < 0) {
        this.isHeaderFixed = false;
        this.navbarMobileFixed = true;
      } else {
        this.isHeaderFixed = true;
        this.navbarMobileFixed = false;
      }
      // 吸顶效果
      if (scrollTop > offsetTop) {
        this.isFixed = true;
        // this.navbarMobileFixed = true;

        // this.isHeaderFixed = true;
      } else {
        this.isFixed = false;
        this.isHeaderFixed = false;
        // this.navbarMobileFixed = false;
      }
    },
    Todrps(val) {
      this.DropdownItemBtnStatus = false;
      this.DropdownItemBtnStatus2 = false;
      this.DropdownItemBtnStatus3 = false;
      this.DropdownItemBtnStatus4 = false;
      this.DropdownItemBtnStatus5 = false;
      this.DropdownItemBtnStatus6 = false;
      this.mainDropdownBtnStatus2 = true;

      if (val == 1) {
        this.DropdownItemBtnStatus = true;
      } else if (val == 2) {
        this.DropdownItemBtnStatus2 = true;
      } else if (val == 3) {
        this.DropdownItemBtnStatus3 = true;
      } else if (val == 4) {
        this.DropdownItemBtnStatus4 = true;
      } else if (val == 5) {
        this.DropdownItemBtnStatus5 = true;
      } else if (val == 6) {
        this.DropdownItemBtnStatus6 = true;
      }

      if (val == "返回") {
        this.mainDropdownBtnStatus2 = false;
        this.DropdownItemBtnStatus = false;
        this.DropdownItemBtnStatus2 = false;
        this.DropdownItemBtnStatus3 = false;
        this.DropdownItemBtnStatus4 = false;
        this.DropdownItemBtnStatus5 = false;
        this.DropdownItemBtnStatus6 = false;
      }
    },
    ShowDropdown(val) {
      this.DropdownItemBtnStatus = false;
      this.DropdownItemBtnStatus2 = false;
      this.DropdownItemBtnStatus3 = false;
      this.DropdownItemBtnStatus4 = false;
      this.DropdownItemBtnStatus5 = false;
      this.DropdownItemBtnStatus6 = false;
      if (val == "hide") {
        this.DropdownBtnStatus = false;
        this.mainDropdownBtnStatus = false;
      } else {
        this.DropdownBtnStatus = true;
        this.mainDropdownBtnStatus = true;
        this.mainDropdownBtnStatus2 = false;
      }
    },
    cdrp() {
      this.showcdrpstatus1 = true;
    },
    showRegform() {
      this.$store.commit("showRegform", true);
    },
    closeNavbarMobile() {
      this.isClose = true;
    }
  }
};
</script>
