<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      isShow: false,
      isShowImg: false,
      isShowSuvery: false,

      showsmallStatus: true,

      screenWidth_mobilepage: ""
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    screenWidth_mobilepage: {
      handler: function (val, oldVal) {
        console.log(val, "s");
        if (val < 1014) {
          this.showsmallStatus = false;
          this.$store.commit("dontShowPo", false);
        } else {
          this.showsmallStatus = true;
          this.$store.commit("dontShowPo", true);
        }

        immediate: true;
      }
    }
  },
  //方法集合
  methods: {
    initDataS() {
      console.log("s,init");
      let _this = this;
      this.screenWidth_mobilepage = document.body.clientWidth;
      window.onresize = () => {
        this.screenWidth_mobilepage = document.body.clientWidth;
      };
    },
    //最新优惠
    newpreFerential(val) {
      if (val == 1) {
        this.isShow = !this.isShow;
        this.isShowImg = false;
      } else {
        this.isShowImg = !this.isShowImg;
        this.isShow = false;
      }
    },
    showsmall() {
      this.showsmallStatus = !this.showsmallStatus;
      this.isShowImg = false;
      this.isShow = false;
    },
    showSurvey() {
      this.isShowSuvery = !this.isShowSuvery;
    },
    showRegform() {
      this.$store.commit("showRegform", true);
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    let _this = this;
    this.$nextTick(function () {
      _this.initDataS();
    });
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>

