    // screenWidth: {
    //   handler: function (val, oldVal) {
    //     console.log(val, "M");
    //     if (val < 1014) {
    //       this.benefits_view = true;
    //     } else {
    //       this.benefits_view = false;
    //     }
    //     immediate: true;
    //   }
    // }
  },
  //方法集合
  methods: {
    initDataM() {
      let _this = this;
      this.screenWidth = document.body.clientWidth;
      window.onresize = () => {
        this.screenWidth = document.body.clientWidth;
      };
      window.mParticle &&
        window.mParticle.logPageView("FXTM landing page", {
          origin: window.location.origin,
          page: window.location.pathname,
          domain: window.location.hostname,
          path: window.location.pathname,
          referrer: document.referrer,
          search: window.location.search,
          title: document.title,
          url: window.location.href
        });
      var myswiper2 = new swiper("#swiper2", {
        slidesPerView: 1,
        autoplay: true,
        loop: true,

        onSlideChangeEnd: function (swiper) {
          swiper.update();
          myswiper2.startAutoplay();
          myswiper2.reLoop();
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      });
    },
    showSurvey() {
      this.isShowSuvery = !this.isShowSuvery;
    },
    showRegform() {
      this.$store.commit("showRegform", true);
    },
    scrollEvent() {
      let element = document.getElementById("myVideo");
      let viewHeight = window.innerHeight;
      let elementTop = element.getBoundingClientRect().top;

      // element.addEventListener(
      //   "ended",
      //   function () {
      //     element.load();
      //   },
      //   false
      // );

      if (!this.isPlayed) {
        if (elementTop - viewHeight < 0) {
          //达到目标区域

          this.autoPlay = true;
          element.play();
          this.isPlayed = true;
          console.log("Scroll event triggered!", this.autoPlay);
        }
      }
    },
    closeRegform() {
      this.$store.commit("showRegform", false);
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    let _this = this;
    window.addEventListener("scroll", this.scrollEvent); // 监听滑动事件
    this.$nextTick(function () {
      _this.initDataM();
      let nav = document.createElement("script");
      nav.type = "application/javascript";
      // nav.src = '../assets/js/iframe_content.js'
      nav.src = require("../assets/js/iframe_content.js");
      document.body.appendChild(nav);
    });
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
